let wordModal = document.querySelector("._word-modal")
let sentenceModal = document.querySelector("._sentence-modal")

let articleText = document.querySelector(".article .text")

if(articleText){
    articleText.onmouseup = function(e) {
        let selected_text = "";

        wordModal.style.top = `${e.clientY / 16}em`
        sentenceModal.style.top = `${e.clientY / 16}em`

        if(window.getSelection) {

            selected_text = window.getSelection().toString();

        } else if(document.selection && document.selection.type != "Control") {

            selected_text = document.selection.createRange().text;

        }

        if(!!selected_text) {
            selected_text = selected_text.trim()
            let wordsArray = selected_text.split(' ')

            if(wordsArray.length === 1){
                selected_text = selected_text.replace(/[!"#$%&()*+,./:;<=>?@[\]^_`{|}~]/g, '')
                if(selected_text.length === 0){
                    return
                }
                sentenceModal.classList.remove("_active")
                wordModal.classList.add("_active")
            } else{
                wordModal.classList.remove("_active")
                sentenceModal.classList.add("_active")
            }
        }

    };

    document.addEventListener( 'click', (e) => {
        const withinBoundaries = e.composedPath().includes(articleText);

        if ( ! withinBoundaries ) {
            sentenceModal.classList.remove("_active")
            wordModal.classList.remove("_active")
        }
    })

    document.addEventListener('keydown', function(e) {
        if( e.keyCode == 27 ){
            sentenceModal.classList.remove("_active")
            wordModal.classList.remove("_active")
        }
    });

}



